.subjectDetails{
    width: 70%;
}

.subject-details-nav{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 6px;
    padding: 6px;
}

.subject-details-nav-item{
    display: flex;
    align-items: start;
    justify-content: start;
    padding: 3px 10px;
    margin: 3px;
    height: 30px;
    border-radius: 3px;
    width: auto;
    background-color: lightblue;
    color: blue;
    font-size: large;
    border: 1px solid blue;
    cursor: pointer;
}

.subject-details-nav-item:hover{
    color: white;
    background-color:blue;
}

.activeTopicItems{
    color: white;
    background-color:blue;
}

@media screen and (min-width: 712px) {
    .subject-details-nav{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        margin: 6px;
        padding: 6px;
    }
}