.class{
    display: flex;
    flex-wrap: wrap;
    border: 3px solid aqua;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    padding: 6px;
    cursor: pointer;
}

.class div{
    padding: 3px;
}

.class-number{
    width: 300px;
    padding: 6px;
    border-radius: 10px;
    font-family:'Lucida Sans', 'Lucida Sans Regular';
    cursor: pointer;
}
.class-number:hover{
    background-color: aliceblue;
}

.class-subjects{
    list-style: none;
    cursor: pointer;
    padding: 6px;
    border-radius: 10px;
}

.class-subjects:hover{
    background-color: aqua;

}

.close-modal{
    width: 100px;
    height: 30px;
    background-color: aqua;
    border-radius: 10px;
}
@media screen and (min-width: 712px) {
 .class{
    flex-wrap: nowrap;
 }
 .class span{
    flex-wrap: nowrap;
 }

}