.subTopics{
    width: 30%;
    text-align: center;
}
.subject-topics{
    padding: 10px;
    border-radius: 10px;
    font-family:'Lucida Sans', 'Lucida Sans Regular';
    cursor: pointer;
}

.subject-topics:hover{
    background-color: aliceblue;
}

.sub-topics{
    list-style: none;
    cursor: pointer;
    padding: 6px;
    border-radius: 10px;
}

.sub-topics:hover{
    background-color: aqua;
}

.activeSubTopic{
    transition: background-color 0.3s ease;
    background-color: aqua;
}