.navbar{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    border: 1px solid gray;
}


.nav-left{
    display: flex;
    flex-direction: row;
    width: 70%;
    height: 50%;
    justify-content: center;
    align-items: center;
}

.nav-right{
    display: flex;
    flex-direction: column;
    width: 70%;
    height: 50%;
    justify-content: center;
    align-items: center;
}

.nav-left .nav-img{
    display: flex;
    width:  50%;
}
.nav-img img{
    min-width: 10px;
    min-height: 10px;
}
.nav-left >div{
    width: 100%;
}

.nav-right input{
    height: 30px;
    border-radius: 30px;
    width: 100%;
    padding: 10px;
    margin: 10px;
    border: 1px solid black;
}

@media screen and (min-width: 712px) {
    .navbar {
        display: flex;
      flex-direction: row;
    }
    .nav-left{
        display: flex;
        flex-direction: row;
        width: 30%;
        justify-content: center;
          align-items: center;
    }
    .select-class{
        display: flex;
        align-items: center;
        justify-content: center;
    
    }
    .nav-right{
        display: flex;
        width: 70%;
    }
    .nav-right input{
        height: 30px;
        border-radius: 30px;
        width: 90%;
        padding: 10px;
        margin: 10px;
        border: 1px solid black;
    }
    .nav-img img{
        min-height: 50%;
        min-width: 50%;
    }
    
  }

